import * as yup from 'yup';

let values: Array<any> = [];

export default yup
  .object({
    mood: yup
      .array()
      .of(yup.string().oneOf((values = ['happy', 'sad', 'surprised'])))
      .meta({
        type: 'checkbox',
        values: values,
        defaultValue: []
      })
  })
  .noUnknown();
